import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect,useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFSelect} from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
RechargeCardSe.propTypes = {
    discount: PropTypes.object,
  };
export default function RechargeCardSe({ discount}) {
    const isMountedRef = useIsMountedRef();
    const [allvend,SetVend] = useState([{key: 'Adex1', name: ''}]);
  const NewUserSchema = Yup.object().shape({
    mtn: Yup.string().required('MTN  VENDING REQUIRED'),
    airtel: Yup.string().required('AIRTEL VENDING REQUIRED'),
    glo: Yup.string().required('GLO  VENDING REQUIRED'),
    mobile: Yup.string().required('9MOBILE VENDING REQUIRED'),
 });
  const defaultValues = useMemo(
    () => ({
    mtn: discount?.recharge_card?.mtn || '',
    airtel: discount?.recharge_card?.airtel || '',
    glo: discount?.recharge_card?.glo|| '',
    mobile: discount?.recharge_card?.mobile || '',
    }),
    [discount]
  );
  const AccessToken =  window.localStorage.getItem('accessToken');
  const methods = useForm({ resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount!== undefined) {
      reset(defaultValues);
      SetVend(discount?.adex_code)
    }
   
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]); 
  const {
    reset,
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/edit/recharge_card_sel/account/${AccessToken}/secure`,data)
     swal('success', `Updated`, 'success');
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };

   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
     
        <Grid item xs={12} md={8}>
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >     
              <RHFSelect name="mtn" label="MTN" defaultValue="Adex1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>         
                 
              <RHFSelect name="airtel" label="AIRTEL" defaultValue="Adex1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>  
              <RHFSelect name="glo" label="GLO" defaultValue="Adex1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>  
              <RHFSelect name="mobile" label="9MOBILE" defaultValue="Adex1">
                <option value="" />
                {allvend.map((option) => (
                  <option key={option.key} value={option.key}>
                    {option.name}
                  </option>
                ))}
              </RHFSelect>  
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
