// @mui
import { Container,Backdrop,CircularProgress } from '@mui/material';
import { useState, useEffect, } from 'react';
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import axios from '../../utils/axios';
import RechargeCardSe from '../../sections/admin/recharge_card_sel';

export default function  RechargeCardSel(){
    const { themeStretch } = useSettings(); 
    const [discount, Setdiscount] = useState();
    const [load,SetLoad] = useState(true)
    const AccessToken =  window.localStorage.getItem('accessToken'); 
    useEffect(()  => {
        const initialize = async () => {
       await axios.get(`/api/recharge_card_sel/adex/${AccessToken}/recharge_card`).then(data => {
              Setdiscount(data.data);
              SetLoad(false)
             }).catch(() => SetLoad(false));
            }
        initialize();
        }, [AccessToken, Setdiscount]);  
    return (
      <Page title="Admin || RECHARGE CARD VENDING SELECTION">
        <Container maxWidth={themeStretch ? false : 'lg'}>
          <HeaderBreadcrumbs
            heading={'RECHARGE CARD VENDING SELECTION'}
            links={[
              { name: 'Dashboard', href: PATH_ADMIN.root },
              { name: 'adex-system'},
            ]}
          />
          <RechargeCardSe discount={discount}/>
          <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
        </Container>
      </Page>
    );
}